.header-container {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 40px;

  .logo {
    display: flex;
    justify-content: center;
    overflow: hidden;

    img {
      width: 150px;
      height: 150px;
      object-fit: cover;
    }
  }
}